:root {
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color-scheme: light dark;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}
*, *::before, *::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  background: var(--color-background-dark);
  color: #ededed;
  font-family: 'Inter', sans-serif;
}
html, body {
  width: 100%;
  overflow-x: hidden;
}
img, video, canvas, svg {
  max-width: 100%;
  height: auto;
  display: block;
}
a {
  font-weight: 500;
  text-decoration: inherit;
}
h1 {
  font-size: 3.2em;
  line-height: 1.1;
}
button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  cursor: pointer;
  transition: border-color 0.25s;
}
.glass-card {
  background-color: rgba(247, 125, 43, 0.1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(247, 125, 43, 0.2);
}
.glass-button {
  background-color: rgba(247, 125, 43, 0.1);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(247, 125, 43, 0.2);
  transition: background-color 0.3s, border-color 0.3s;
}
.glass-button:hover {
  background-color: rgba(247, 125, 43, 0.2);
  border-color: rgba(247, 125, 43, 0.3);
}
.glassmorphism {
  background: rgba(39, 32, 27, 0.5);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(85, 69, 58, 0.3);
}
.glow {
  box-shadow: 0 0 30px 0px rgba(247, 125, 43, 0.3);
}
.shadow-glow {
  box-shadow: 0 0 20px rgba(247, 125, 43, 0.3);
}
.masonry-grid {
  column-count: 1;
  column-gap: 1.5rem;
}
@media (min-width: 640px) {
  .masonry-grid {
    column-count: 2;
  }
}
@media (min-width: 1024px) {
  .masonry-grid {
    column-count: 3;
  }
}
@media (min-width: 1280px) {
  .masonry-grid {
    column-count: 4;
  }
}
.masonry-item {
  break-inside: avoid;
  margin-bottom: 1.5rem;
}
@keyframes gradient {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
@keyframes pulse-slow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-gradient {
  background-size: 200% 200%;
  animation: gradient 3s ease infinite;
}
.animate-pulse-slow {
  animation: pulse-slow 4s ease-in-out infinite;
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
.animate-slide-up {
  animation: slide-up 0.6s ease-out;
}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background: #221710;
}
::-webkit-scrollbar-thumb {
  background: #f77d2b;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
  background: #ff8c3a;
}
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
.gradual-blur-inner {
  position: relative;
  width: 100%;
  height: 100%;
}
.gradual-blur-inner > div {
  -webkit-backdrop-filter: inherit;
  backdrop-filter: inherit;
}
.gradual-blur {
  isolation: isolate;
}
@supports not (backdrop-filter: blur(1px)) {
  .gradual-blur-inner > div {
    background: rgba(0, 0, 0, 0.3);
    opacity: 0.5;
  }
}
.gradual-blur-fixed {
  position: fixed !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1000;
}
.section {
  padding-top: 80px;
  margin-bottom: 80px;
}
.parallax {
  transform: translateZ(-5px) scale(1.05);
}
.parallax-container {
  perspective: 1000px;
}
